body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.form-container {
  width: 650px;
  padding: 3% 3% 2% 2%;
  margin-top: 5%;
  background-color: #EFEFEF;
}

.space {
  padding: 0;
}

.recipe-ctn {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  padding: 20px;
}

.recipe-item {
  cursor: pointer;
  padding: 20px;
  background-color: #F8F8F8;
  border-radius: 5%;
}
.recipe-item:hover {
  background-color: #EFEFEF;
}

.recipe-img {
  width: 100%;
  height: 200px;
  border-radius: 5%;
  object-fit: cover;
}

.recipe-title {
  padding: 20px 0 0 0;
}

.delete-btn {
  position: absolute;
  margin: 10px;
  right: 15px;
  color: #BABABA;
  cursor: pointer;
}

.form-btn {
  border-radius: 50%;
  background-color: #FFF;
  margin-left: 5px;
}

.modal-body {
  text-align: center;
}

.modal-text {
  text-align: left;
}

.modal-image {
  width: 50%;
}

.add-info {
  margin-top: 10px;
}

.ingredient-list {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
}

.table {
  width: 80%;
  margin: 20px 0;
}

